.App {
  text-align: center;
  background-color: #ececec;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: rgb(29, 29, 29);
}

h1.home-profile-description {
  margin-top: 0px;
}

.home-profile-description {
  margin-bottom: 45.614px;
}

p.home-profile-description {
  font-size: 30px;
}

#pablo-memoji {
  width: 200px;
}

p.home-liked-stuff-description {
  margin-bottom: 72px;
  font-size: 30px;
}

.container-home-media-cards {
  display: flex !important;
  gap: 12px;
}

.home-footer {
  font-size: 12px;
  margin-top: 48px;
}

.card-component {
  box-shadow: none !important;
  cursor: pointer;
}

.card-component:hover {
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.103) !important;
}

a {
  text-decoration: none;
}
